let log_entry_password = '126553';
let resetKey = '111111';
let currKey = '';

export default log_entry_password;

export function authenticate(text){
    currKey += text;

    if(currKey.length > log_entry_password.length){
        currKey = currKey.substring(1,currKey.length);
    }

    if(currKey == resetKey){
        currKey = '';
    }
    return currKey == log_entry_password;
}

export function getPort(){
    return 3000;
}

export function getServerIP(){
    return 'vcm-20229.vm.duke.edu'
}

export function guid(){
    let s4 = () => {
        return Math.floor((1 + Math.random()) * 0x10000)
            .toString(16)
            .substring(1);
    }
    //return id of format 'aaaaaaaa'-'aaaa'-'aaaa'-'aaaa'-'aaaaaaaaaaaa'
    return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4();
}
